import { Avatar, Button, Dropdown, Layout, Menu, Typography } from "antd";
import { useEffect, useState } from "react";
import LoginForm from "./components/LoginForm";
import SignupForm from "./components/SignupForm";
import MyCart from "./components/MyCart";
import FoodList from "./components/FoodList";
import { logout } from "./utils";
import { UserOutlined } from "@ant-design/icons";

const { Header, Footer, Content } = Layout;
const { Title } = Typography;
function App() {
  const [authed, setAuthed] = useState();
  const [userInfo, setUserInfo] = useState({});
  useEffect(() => {
    checkLogin();
  }, [authed]);

  const checkLogin = async () => {
    const SERVER_ORIGIN = process.env.REACT_APP_SERVER_ORIGIN;
    const checkURL = `${SERVER_ORIGIN}/user/me`;
    const loginToken = localStorage.getItem("login_token");
    if (loginToken === "true") {
      setAuthed(true);
    } else {
      setAuthed(false);
    }
    try {
      const res = await fetch(checkURL, {
        method: "GET", // Method is optional if you're making a simple GET request
        credentials: "include", // Include cookies with the request
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (res.ok) {
        const data = await res.json();
        setUserInfo(data);
        setAuthed(true);
      } else {
        setAuthed(false);
      }
    } catch (error) {}
  };

  const handleLogout = async () => {
    try {
      await logout();
      setAuthed(false);
      localStorage.removeItem("login_token");
    } catch (err) {
      console.error(err);
    }
  };

  const menu = userInfo ? (
    <Menu>
      <Menu.Item key={1}>Hi, {userInfo.first_name}! </Menu.Item>

      <Menu.Item key={2} style={{ marginBottom: "10px" }}>
        {userInfo.email}
      </Menu.Item>

      <Menu.Item key={3} onClick={handleLogout}>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            width: "100%",
          }}
        >
          <Button shape="round" style={{ width: "5rem" }}>
            Log out
          </Button>
        </div>
      </Menu.Item>
    </Menu>
  ) : (
    <></>
  );

  return (
    <Layout style={{}}>
      <div
        className="hero-background"
        style={{
          backgroundImage: `url("/images/bg.png")`,
          backgroundSize: "cover",
          backgroundPosition: "center",

          height: 600,
          width: "100%",
        }}
      >
        <Header
          style={{
            background: "transparent",
            display: "flex",
            justifyContent: "space-between",
            marginTop: "20px",
          }}
        >
          <Title
            level={1}
            style={{
              backdropFilter: "blur(1px)",
              lineHeight: "inherit",
              marginBottom: 0,
              marginLeft: "10px",
              scale: "1.1",
              textShadow: "1px 2px 1px rgba(0, 0, 0, 0.5)",
            }}
          >
            <div
              style={{
                color: "white",
                display: "flex",
                alignItems: "center",
              }}
            >
              <img
                src="/images/logo.svg"
                alt="logo"
                style={{ width: "50px", height: "50px" }}
              />
              &nbsp;GreenHug
            </div>
          </Title>
          <div>
            {authed ? (
              <>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    marginTop: "10px",
                  }}
                >
                  <MyCart />

                  <Dropdown overlay={menu}>
                    <Avatar
                      style={{
                        cursor: "pointer",
                        backgroundColor: "#177038",
                        marginLeft: "10px",
                      }}
                      icon={<UserOutlined style={{ fontSize: 24 }} />}
                      size="large"
                    ></Avatar>
                  </Dropdown>
                </div>
              </>
            ) : (
              <>
                <LoginForm onSuccess={() => setAuthed(true)} />
                <SignupForm />
              </>
            )}
          </div>
        </Header>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            marginLeft: "50px",
            justifyContent: "center",
            alignItems: "start",
            height: "60vh",
            width: "60%",
          }}
        >
          <Title
            level={1}
            style={{
              fontFamily: "Helvetica, sans-serif",
              color: "white",
              backdropFilter: "blur(1px)",
              fontSize: "4rem",
              fontWeight: 800,
              textShadow: "2px 2px 2px rgba(200, 150, 0, 0.5)",
              fontVariantCaps: "all-small-caps",
            }}
          >
            Embrace the Taste of Freshness
          </Title>
          <Title
            level={3}
            style={{
              fontFamily: "Helvetica, sans-serif",
              color: "rgb(250, 225, 120)",
              marginTop: 0,
              fontSize: "2rem",
              backdropFilter: "blur(1px)",
              textShadow: "1px 1px 1px rgba(0, 0, 0, 0.7)",
            }}
          >
            Flavors at Your Fingertips
          </Title>
        </div>
      </div>
      <Content
        style={{
          padding: "50px",
          maxHeight: "calc(100% - 64px)",
          overflowY: "auto",
        }}
      >
        <FoodList authed={authed} />
      </Content>

      <Footer />
    </Layout>
  );
}

export default App;
